*{
  margin: 0px;
  padding: 0px;
}

.app{
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.board{
  width: calc(64px  *8);
  height: calc(64px * 8);
  display: flex;
  flex-wrap: wrap;
}

.cell{
  width: 64px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cell img{
  width: 48px;
  height: 48px;
  position: relative;
}

.black{
  background-color: deepskyblue;
}

.white{
  background-color: bisque;
}

.selected{
  background-color: rgb(99, 44, 44);
}

.available{
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background-color: rgb(82, 255, 47);
}

.lost {
  height: calc(50vh - 60px);
  padding: 30px;
  margin-left: 50px;
  background-color: lightgray;
}